import React, { useEffect, useState } from "react";
import { localRoute } from '../../../routes/localRoutes';
import AOS from "aos";
import "aos/dist/aos.css";
import { useHistory } from 'react-router-dom';

const FaqItem = ({ id, question, answer, isCollapsed, toggleCollapse }) => {
    return (
        <div class="container mt-40">
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <h3 class="font-montserrat fs-20 fw-700" onClick={() => toggleCollapse(id)} style={{ color: '#BABABA', cursor: 'pointer' }}>
                    {question}
                </h3>
                <h3 class="font-montserrat" onClick={() => toggleCollapse(id)} style={{ color: '#BABABA', cursor: 'pointer', marginRight: '10px' }}>
                    {isCollapsed ? <>&#9660;</> : <>&#9650;</>}
                </h3>
            </div>
            <hr style={{ borderColor: '#BABABA' }} />
            {!isCollapsed && <p class="font-montserrat fs-16 fw-300" style={{ color: '#fff' }}>
                {answer}
            </p>}
        </div>
    );
};

export default function AskQuestion() {
    const history = useHistory()

    useEffect(() => {
        window.scrollTo(0, 0)
        AOS.init({
            duration: 2000
        });
    }, [])

    const [faqItems, setFaqItems] = useState([
        {
            id: 0,
            question: "how long do we get to keep the cars for the wedding or baraat package?",
            answer: "our wedding or baraat package offers flexible rental durations, ranging from a few hours to a full day or more, tailored to your needs. Contact us for specific details.",
            isCollapsed: true
        },
        {
            id: 1,
            question: "what is the cancellation policy?",
            answer: "our cancellation policy allows you to cancel your booking up to 48 hours before the rental period begins for a full refund. Cancellations made within 48 hours are subject to a cancellation fee.",
            isCollapsed: true
        },
        {
            id: 2,
            question: "how long do we get to keep the cars for the wedding or baraat package?",
            answer: "our wedding or baraat package offers flexible rental durations, ranging from a few hours to a full day or more, tailored to your needs. Contact us for specific details.",
            isCollapsed: true
        },
        {
            id: 3,
            question: "what is the cancellation policy?",
            answer: "our cancellation policy allows you to cancel your booking up to 48 hours before the rental period begins for a full refund. Cancellations made within 48 hours are subject to a cancellation fee.",
            isCollapsed: true
        }
    ]);

    const toggleCollapse = (id) => {
        setFaqItems(prevState =>
            prevState.map(item =>
                item.id === id ? { ...item, isCollapsed: !item.isCollapsed } : item
            )
        );
    };

    return (
        <div class="mt-50">
            <div class="container feature-blog-one blog-page-bg">
                <div style={{ textAlign: 'center' }}>
                    <h2 class="font-montserrat fs-40 fw-600" style={{ color: '#FFF' }}>
                        frequently asked questions
                    </h2>
                    <p class="font-montserrat" style={{ color: '#BABABA' }}>
                        these are the questions we ve received from our real time customers about renting an exotic car from monaco experiences
                    </p>
                </div>

                {faqItems.map(item => (
                    <FaqItem
                        key={item.id}
                        id={item.id}
                        question={item.question}
                        answer={item.answer}
                        isCollapsed={item.isCollapsed}
                        toggleCollapse={toggleCollapse}
                    />
                ))}
            </div>
        </div >
    )
}
